body {
  background-color: #0f2442;
  color: white;
  padding: 2% 27%;
  font-family: 'Open Sans';
  display: flex;
  justify-content: center; }

a {
  text-decoration: none;
  color: white; }

a:hover {
  text-decoration: underline; }

a:active {
  text-decoration: underline; }

@media screen and (max-width: 400px) {
  body {
    padding: 2% 10%; } }

@media screen and (max-width: 1400px) {
  body {
    padding: 2% 5%; } }

h1 {
  margin: 0px; }

.flex {
  display: flex;
  align-items: center; }

.header-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between; }

@media screen and (max-width: 400px) {
  .header-wrapper {
    justify-content: center; } }

.rounded {
  border: 1px solid white;
  border-radius: 8px; }

.content-wrapper {
  display: flex;
  flex-direction: column; }

.store-button {
  display: flex;
  align-items: center;
  height: 50px; }

.store-button img {
  height: 55px; }

.column {
  display: flex;
  flex-direction: column; }

.logo-text {
  font-size: 26px;
  font-weight: 600; }

.grey {
  color: rgba(255, 255, 255, 0.3); }

.red {
  color: #ff4048; }

.green {
  color: #41b549; }

.blue {
  color: #207abf; }

.title {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  justify-content: space-between; }

.title-text {
  flex: 1;
  flex-wrap: wrap;
  font-size: 26px;
  margin-top: 10px;
  font-weight: 600;
  justify-content: space-between; }

.description {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.9);
  margin-top: 10px; }

.date-n-store {
  display: flex;
  flex: 1;
  flex-direction: row;
  margin-top: 10px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }

.date {
  font-size: 16px;
  font-weight: 600;
  flex: 1; }

.twitter {
  display: flex;
  flex: 1;
  align-items: center; }

.twitter span {
  font-size: 11px;
  opacity: 0.8;
  margin-left: 7px; }

.twitter img {
  width: 16px;
  height: 14px; }

.store {
  display: flex;
  flex: 1;
  justify-content: space-between; }

.confidence-container {
  flex: 1;
  max-width: 310px;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  margin-left: 50px; }

@media screen and (max-width: 400px) {
  .confidence-container {
    margin: 10px; } }

.confidence-header {
  display: flex;
  align-self: stretch;
  border-bottom: 1px solid;
  padding: 9px 30px;
  justify-content: center;
  font-size: 15px;
  font-weight: 600; }

.confidence-content {
  display: flex;
  flex: 1; }

.confidence-block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex: 1; }

.confidence-block.red {
  background-color: rgba(234, 22, 22, 0.2); }

.confidence-block.blue {
  background-color: rgba(44, 144, 215, 0.2);
  border-left: 1px solid white;
  border-right: 1px solid white; }

.confidence-block.green {
  background-color: rgba(94, 172, 3, 0.2); }

.confidence-text {
  font-size: 16px; }

.confidence-digit {
  font-size: 30px; }

.confidence-text.green,
.confidence-digit.green {
  color: #89ff00; }

.confidence-text.blue,
.confidence-digit.blue {
  color: #0095ff; }

.confidence-text.red,
.confidence-digit.red {
  color: red; }

.cards-section {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }

@media screen and (max-width: 400px) {
  .cards-section {
    justify-content: center; } }

.card-wrapper {
  border-width: 0.5px;
  border-color: rgba(255, 255, 255, 0.4);
  flex: 1;
  margin-top: 20px;
  max-width: 225px; }

@media screen and (max-width: 400px) {
  .card-wrapper {
    min-width: 300px; } }

.card-wrapper img {
  width: 42px;
  height: 42px;
  border-radius: 21px;
  margin-right: 12px; }

.card-header {
  padding: 12px;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.title-wrapper {
  display: flex;
  flex-direction: column; }

.card-wrapper .name {
  font-size: 14px;
  font-weight: bold; }

.card-wrapper .rating {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6); }

.rating span {
  color: white;
  padding: 0px 5px;
  border-style: none;
  border-radius: 3px;
  margin-left: 3px; }

.rating .buy {
  background-color: #41b549; }

.rating .hodl {
  background-color: #207abf; }

.rating .sell {
  background-color: #ff4048; }

.rank {
  font-size: 24px;
  font-weight: 100;
  margin-top: -13px;
  margin-right: -8px; }

.card-section {
  padding: 7px 12px;
  border-top: 0.5px solid rgba(255, 255, 255, 0.4); }

.card-section .title {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.4);
  font-weight: 100; }

.bars {
  margin-top: 5px; }

.bars span {
  font-weight: bold;
  font-size: 11px; }

.bar-line {
  flex-direction: row;
  justify-content: space-between;
  flex: 1; }

.bar {
  flex: 1;
  max-width: 121px;
  margin-right: 12px;
  background-color: #263750;
  border-radius: 8px; }

.bar div {
  border-radius: 8px;
  height: 8px; }

.bar .green {
  background-color: #41b549; }

.bar .blue {
  background-color: #207abf; }

.bar .red {
  background-color: #ff4048; }

.forecast {
  margin-top: 5px;
  justify-content: space-between; }

.forecast-block {
  display: flex;
  flex-direction: column; }

.forecast-block span {
  font-size: 11px; }

.forecast-block .header {
  font-weight: bold;
  color: rgba(255, 255, 255, 0.3); }

.forecast-block .value {
  align-self: flex-start; }

.forecast-block .triangle {
  font-size: 9px; }

.predict-section {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap; }

.predict-card {
  border-width: 0.5px;
  border-color: rgba(255, 255, 255, 0.4);
  flex: 1;
  margin-top: 20px;
  max-width: 390px;
  background-color: rgba(0, 0, 0, 0.3); }

@media screen and (max-width: 400px) {
  .predict-card {
    min-width: 300px; } }

.predict-card .header {
  padding: 10px 16px;
  display: flex;
  justify-content: space-between; }

.predict-card .header span {
  font-style: italic;
  font-size: 1.7vh; }

.predict-card .header .column-title {
  font-weight: bold;
  opacity: 0.3;
  color: white;
  font-size: 1.5vh;
  font-style: normal; }

.predict-card .content {
  padding: 7px 12px;
  border-top: 0.5px solid rgba(255, 255, 255, 0.4); }

.predict-card .content .line {
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 7px; }

.predict-card .content .line .line-bar {
  margin-left: 30px;
  display: flex;
  flex-direction: row;
  flex: 1;
  justify-content: flex-end; }

.predict-card .content span {
  opacity: 0.8;
  color: white;
  font-size: 1.2vh; }

.predict-card .content .digit {
  opacity: 0.3;
  color: white;
  font-weight: bold; }

.table-section {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap; }

.table-row {
  justify-content: space-evenly;
  align-self: stretch; }

.table-row.dark {
  background-color: #0b192e; }

.table-row.light {
  background-color: #102442; }

.row-group {
  flex: 1;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: center; }

.row-group.name {
  flex: 1.5;
  justify-content: flex-start;
  padding-left: 30px; }

.row-group img {
  margin-left: 20px;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  margin-right: 12px; }

@media screen and (max-width: 400px) {
  .row-group img {
    width: 10px;
    height: 10px;
    border-radius: 5px; } }

.row-group span {
  font-size: 1.3vh; }

.row-group .order {
  font-size: 1.5vh;
  white-space: nowrap; }

.row-group .triangle {
  font-size: 1vh; }

.table-row.header span {
  text-align: center;
  font-weight: bold;
  font-size: 1.3vh;
  color: #566476; }

footer {
  display: flex;
  align-items: center; }

.footer-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 30px 0; }

@media screen and (max-width: 400px) {
  .footer-wrapper {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center; } }

.footer-wrapper .logo-wrapper {
  margin-top: 20px;
  display: flex;
  flex: 1;
  flex-direction: column; }

.footer-wrapper .logo-wrapper .description {
  font-size: 1.4vh;
  font-weight: 600; }

.disclamer-wrapper {
  margin-top: 20px;
  display: flex;
  flex: 1; }

.disclamer-wrapper span {
  color: #e9ebf1;
  opacity: 0.5;
  font-size: 1.2vh; }
